body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 98%;
  margin: auto;
}
.links{

list-style-type: none;
display: flex;
border: 2px solid black;
padding: 8px;;
background-color: blueviolet;
margin-bottom: 10px;
border-radius: 5px;
font-size: 16px;

font-size: calc(15px + 0.390625vw);
} 
.links li{
margin: 10px;


}
.links li a{
text-decoration: none;
color: chartreuse;
font-weight: bold;
  }
  .login{
    margin-left: 0px;
  }


.mainbar{
  display: flex;
  flex-direction: row-reverse;
}
.cart{
  border: 5px solid blue;
  position: absolute;
  right: 0%;
  top: 150px;
  background-color: beige;
  border-radius: 5%;
}
.cart td{
  border: 3px solid red;
  border-radius: 5%;
  padding: 10px;
}
.ShoppingCartIcon{
position: absolute;
right: 3%;
margin-top: 5px;
border: 2px solid red;
padding: auto;
}
.searchBar{
  width: 90%;
  margin-bottom: 8%;
height: 35px;
border: 3px solid black;
border-radius: 10px;
font-weight: bold;
font-size: larger;
}
.popup{
font-size: xx-large;
    -webkit-animation: cssAnimation 5s forwards; 
    animation: cssAnimation 5s forwards;
}
@keyframes cssAnimation {
    0%   {opacity: 1;}
    90%  {opacity: 1;}
    100% {opacity: 0;}
}
@-webkit-keyframes cssAnimation {
    0%   {opacity: 1;}
    90%  {opacity: 1;}
    100% {opacity: 0;}
}
.footer{
    margin-top: 1%;
    width: calc(98%);
    padding: 1%;
    position: relative;
    float: left;
    background-color: rgb(5, 194, 165);
    height: calc(100%);
    border-radius: 10px;
}
.sideBar{
    text-align: center;
    width: calc(28%);
   padding: 1%;
    float: left; 
    background-color: rgb(229, 222, 17);
    display: flex;
    flex-direction: column;
  
}

.Rightbox{
    /* min-height: 464px; */
    margin-top: 0px;
    padding: 1%;
    width: calc(67.8%);
    position: relative;
    float: right;
    /* background-color: rgb(65, 164, 48); */
     text-align: center;   
    border-radius: 10px;
    text-align: justify;
    text-align: center;
}
.Allitems{
    /* background-color: bisque; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;


}
.card{border: 2px solid black; width: 30%;
text-align: center;

}

.card-img-top{width: 290px;height: 240px;

}
.card-title{
    font-weight: bold;
    height: 60px;
}
.card .btn-primary{
    width: 90%;
    vertical-align: bottom;
    font-size: larger;
    color: black;
    background-color: cornflowerblue;
    border-radius: 5px;
    padding: 2%;
    margin-top: 5px;
    
   
}
.card :hover{
    width: 100%;
}


   
